const defaults = require('../defaults');

/**
 * 
 * @param data 
 * @param headers 
 * @param fns 
 * @return {*}
 */
module.exports = function transformData(data, headers, fns) {
  const context = this || defaults;

  fns.forEach((fn) => {
    data = fn.call(context, data, headers);
  });

  return data;
};
